const Gallery = props => (
  <div className="gallery">
    {props.images.map((image, index) => (
      <img key={index} className="tile" src={"/static/gallery/" + image} />
    ))}

    <style jsx>{`
      .gallery {
        display: grid;
        grid-template-columns: repeat(8, 1fr);
        grid-template-rows: repeat(8, 5vw);
        grid-gap: 15px;
      }
    `}</style>
  </div>
);
export default Gallery;
